import categorized from "./categorized.js";
import graduated from "./graduated.js";
import grid from "./grid.js";
import bivariate from "./bivariate.js";
import dynamic from "./dynamic.js";
import basic from "./basic.js";
import {html} from "../../../_snowpack/pkg/uhtml.js";
import {toggleExpanderParent} from "../toggleExpanderParent.js";
const themes = {
  categorized,
  graduated,
  grid
};
export default (layer) => {
  if (!layer.style)
    return;
  if (layer.style.hidden)
    return;
  if (!layer.style.theme && !layer.style.label)
    return;
  const panel = layer.view.appendChild(html.node`
      <div class="drawer panel expandable">`);
  panel.appendChild(html.node`
    <div
      class="header primary-colour"
      onclick=${(e) => {
    e.stopPropagation();
    toggleExpanderParent(e.target, true);
  }}>
      <span>${mapp_dictionary.layer_style_header}</span>
      <button class="btn-header xyz-icon icon-expander primary-colour-filter">`);
  layer.style.themes && panel.appendChild(html.node`
      <div>${mapp_dictionary.layer_style_select_theme}</div>
      <button class="btn-drop">
      <div
        class="head"
        onclick=${(e) => {
    e.preventDefault();
    e.target.parentElement.classList.toggle("active");
  }}>
        <span>${Object.keys(layer.style.themes)[0]}</span>
        <div class="icon"></div>
      </div>
      <ul>
        ${Object.entries(layer.style.themes).map((theme) => html.node`
          <li onclick=${(e) => {
    const drop = e.target.closest(".btn-drop");
    drop.querySelector("span").textContent = theme[0];
    drop.classList.toggle("active");
    layer.style.theme = theme[1];
    applyTheme(layer);
    layer.reload();
  }}>${theme[0]}`)}`);
  layer.style.bringToFront = html.node`
      <button 
        title=${mapp_dictionary.layer_style_bring_to_front}
        style="margin-top: 5px;"
        class="btn-wide primary-colour"
        onclick=${() => layer.bringToFront()}>${mapp_dictionary.layer_style_bring_to_front}`;
  if (!layer.style.theme && layer.style.label)
    panel.appendChild(layer.style.bringToFront);
  if (layer.style.theme)
    applyTheme(layer);
  return panel;
  function applyTheme(layer2) {
    layer2.style.legend && layer2.style.legend.remove();
    layer2.style.bringToFront.remove();
    if (layer2.style.theme && !layer2.style.theme.type)
      return;
    layer2.style.legend = legend(layer2);
    panel.appendChild(layer2.style.legend);
    panel.appendChild(layer2.style.bringToFront);
  }
  function legend(layer2) {
    layer2.style.legend = html.node`<div class="legend">`;
    layer2.filter = layer2.filter || {};
    return themes[layer2.style.theme.type](layer2);
  }
};
