import layerview from "./layerview.js";
import {html} from "../../_snowpack/pkg/uhtml.js";
import {toggleExpanderParent} from "./toggleExpanderParent.js";
export default (params) => {
  const listview = {
    node: params.target,
    groups: {}
  };
  params.list.forEach((layer) => {
    if (layer.hidden)
      return;
    layerview(layer);
    if (!layer.group) {
      listview.node.appendChild(layer.view);
      return;
    }
    if (!listview.groups[layer.group])
      createGroup(layer);
    listview.groups[layer.group].addLayer(layer);
  });
  function createGroup(layer) {
    const group = {
      list: []
    };
    listview.groups[layer.group] = group;
    const drawer = listview.node.appendChild(html.node`<div class="drawer layer-group expandable">`);
    const header = drawer.appendChild(html.node`
      <div
        class="header enabled"
        onclick=${(e) => {
      e.stopPropagation();
      toggleExpanderParent(e.target, true);
    }}>
        <span>${layer.group}`);
    const meta = drawer.appendChild(html.node`<div class="meta">`);
    group.chkVisibleLayer = () => {
      group.list.some((layer2) => layer2.display) ? hideLayers.classList.add("on") : hideLayers.classList.remove("on");
    };
    group.addLayer = (layer2) => {
      if (layer2.groupmeta) {
        const metaContent = meta.appendChild(html.node`<div>`);
        metaContent.innerHTML = layer2.groupmeta;
      }
      group.list.push(layer2);
      drawer.appendChild(layer2.view);
      group.chkVisibleLayer();
      layer2.view.addEventListener("display-on", group.chkVisibleLayer);
      layer2.view.addEventListener("display-off", group.chkVisibleLayer);
    };
    const hideLayers = header.appendChild(html.node`
        <button
          class="btn-header xyz-icon icon-toggle"
          title=${mapp_dictionary.layer_group_hide_layers}
          onclick=${(e) => {
      e.stopPropagation();
      e.target.classList.toggle("on");
      if (e.target.classList.contains("on")) {
        group.list.filter((layer2) => !layer2.display).forEach((layer2) => layer2.show());
        return;
      }
      group.list.filter((layer2) => layer2.display).forEach((layer2) => layer2.remove());
    }}>`);
    header.appendChild(html.node`
        <button 
          class="xyz-icon btn-header icon-expander"
          title=${mapp_dictionary.layer_group_toggle}
          onclick=${(e) => {
      e.stopPropagation();
      toggleExpanderParent(e.target);
    }}>`);
  }
};
