import {html} from "../../_snowpack/pkg/uhtml.js";
import {toggleExpanderParent} from "./toggleExpanderParent.js";
import style from "./style/_styles.js";
export default (layer) => {
  layer.view = html.node`<div class="drawer layer-view">`;
  const header = layer.view.appendChild(html.node`
    <div class="header enabled"><span>${layer.name || layer.key}`);
  header.appendChild(html.node`
    <button
      title=${mapp_dictionary.layer_zoom_to_extent}
      class="btn-header xyz-icon icon-fullscreen"
      onclick=${(e) => {
    e.stopPropagation();
    layer.zoomToExtent();
  }}>`);
  const toggleDisplay = header.appendChild(html.node`
    <button
      title=${mapp_dictionary.layer_visibility}
      class="${`btn-header xyz-icon icon-toggle ${layer.display && "on" || "off"}`}"
      onclick=${(e) => {
    e.stopPropagation();
    layer.display ? layer.remove() : layer.show();
  }}>`);
  layer.view.addEventListener("display-on", () => {
    toggleDisplay.classList.add("on");
  });
  layer.view.addEventListener("display-off", () => {
    toggleDisplay.classList.remove("on");
  });
  if (layer.meta) {
    const meta = layer.view.appendChild(html.node`<p class="meta">`);
    meta.innerHTML = layer.meta;
  }
  style(layer);
  if (layer.view.children.length <= 1)
    return;
  layer.view.classList.add("expandable");
  header.onclick = (e) => {
    e.stopPropagation();
    toggleExpanderParent(e.target, true);
  };
  header.appendChild(html.node`
  <button
    title=${mapp_dictionary.layer_toggle_dashboard}
    class="btn-header xyz-icon icon-expander"
    onclick=${(e) => {
    e.stopPropagation();
    toggleExpanderParent(e.target);
  }}>`);
};
